import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";

import FormKodeRosok from "./Component/FormKodeRosok";
import { connect } from "react-redux";
import { editRosok, getListRosok } from "../actions/rosokAction";
import notrespond from "../Images/500.svg";
import activelogo from "../Images/active.png";
import nonactive from "../Images/nonactive.png";
import Swal from "sweetalert2";
import FormKodeRosokEdit from "./Component/FormKodeRosokEdit";
import Skeleton from "react-loading-skeleton";
import paginationFactory from "react-bootstrap-table2-paginator";
import {
  AxiosMasterDelete,
  AxiosMasterPost,
  AxiosMasterUpdate,
} from "../Axios";
import { berhasil, gagal, LogData } from "../Alert";
import { hideModal, showModal } from "../actions/ModalAction";
import ModalMasterData from "./Component/ModalMasterData";
import HeaderCard from "../HeaderCard";
import TourGuide from "../TourGuide";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;

function status(cell, row) {
  if (row.statusAktif === "AKTIF") {
    return (
      <div>
        <img src={activelogo} style={{ width: 50, height: 50 }} alt="ACTIVE" />
      </div>
    );
  } else {
    return (
      <div>
        <img
          src={nonactive}
          alt="NON ACTIVE"
          style={{ width: 50, height: 50 }}
        />
      </div>
    );
  }
}
function hapus(dispatch, kode) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      let data = {
        data: {
          kodeJenis: kode,
        },
      };
      AxiosMasterDelete("subcategory", data)
        .then((res) =>
          berhasil("Data Berhasil Dihapus!")
            .then(() =>
              LogData("Data Berhasil Dihapus Di Kode Rongsok Dan Ciok", data)
            )
            .then(() => dispatch(getListRosok()))
        )
        .catch((err) =>
          gagal("Data Gagal Dihapus!").then(() =>
            LogData('Data Gagal Dihapus Di Kode Rongsok Dan Ciok"', data, err)
          )
        );
    }
  });
}
const maptoState = (state) => {
  return {
    KodeRosokList: state.provinsi.rosok,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
  };
};
class Koderosok extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Koderosok: [],
      response: "",
      display: "none",
      columns: [
        {
          dataField: "kodeKategori",
          text: "Kode Kategori",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "kodeJenis",
          text: "Kode Jenis",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "kadarCetak",
          text: "Kadar Cetak",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "kadarBeli",
          text: "Kadar Beli",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "kadarJual",
          text: "Kadar Jual",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "namaJenis",
          text: "Nama Jenis",
        },
        {
          dataField: "statusAktif",
          text: "Status",
          sort: true,
          formatter: status,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "link",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  onClick={() => hapus(this.props.dispatch, row.kodeJenis)}
                  data-tut="reactour_3"
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
                <button
                  data-tut="reactour_4"
                  className="btn btn-warning"
                  onClick={() =>
                    this.props.dispatch(
                      editRosok(
                        row.kodeJenis,
                        row.statusAktif,
                        row.kodeKategori,
                        row.namaJenis,
                        row.kadarBeli,
                        row.kadarJual,
                        row.kadarCetak,
                        this.editModal()
                      )
                    )
                  }
                >
                  <i className="fa fa-edit mr-2"></i>
                  EDIT
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getListRosok());
  }

  handleSubit(hasil) {
    let data = {
      kodeKategori: hasil.kodeKategori,
      kodeKelompok: "CIOK",
      kodeJenis: hasil.kodeJenis,
      statusAktif: hasil.statusAktif ? hasil.statusAktif : "AKTIF",
      kadarCetak: hasil.kadarCetak,
      kadarBeli: hasil.kadarBeli,
      namaJenis: hasil.namaJenis,
      kadarJual: hasil.kadarJual,
    };
    AxiosMasterPost("subcategory", data)
      .then((res) =>
        berhasil("Data Berhasil Ditambah!")
          .then(() =>
            LogData("Data Berhasil Ditambah Di Kode Rongosk dan Ciok", data)
          )
          .then(() => this.props.dispatch(hideModal()))
          .then(() => this.props.dispatch(getListRosok()))
      )
      .catch((err) =>
        gagal("Data Gagal Ditambah!")
          .then(() =>
            LogData("Data Gagal Ditambah Di Kode Rongsok dan Ciok", data, err)
          )
          .then(() => this.props.dispatch(hideModal()))
      );
  }
  handleEdit(hasil) {
    let data = {
      kodeKategori: hasil.kodeKategori,
      kodeKelompok: "CIOK",
      kodeJenis: hasil.kodeJenis,
      statusAktif: hasil.statusAktif ? hasil.statusAktif : "AKTIF",
      kadarCetak: hasil.kadarCetak,
      kadarBeli: hasil.kadarBeli,
      namaJenis: hasil.namaJenis,
      kadarJual: hasil.kadarJual,
    };

    AxiosMasterUpdate("subcategory", data)
      .then((res) =>
        berhasil("Data Berhasil Diubah!")
          .then(() =>
            LogData("Data Berhasil Diubah Di Kode Rongsok dan Ciok", data)
          )
          .then(() => this.props.dispatch(hideModal()))
          .then(() => this.props.dispatch(getListRosok()))
      )
      .catch((err) =>
        gagal("Data Gagal Diubah!")
          .then(() =>
            LogData("Data Gagal Diubah Di Kode Rongsok Dan Ciok", data, err)
          )
          .then(() => this.props.dispatch(hideModal()))
      );
  }

  tambahModal() {
    this.props.dispatch(showModal());
    this.setState({ isEdit: false });
  }
  editModal() {
    this.props.dispatch(showModal());
    this.setState({ isEdit: true });
  }

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true">×</span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <ModalMasterData
          content={
            this.state.isEdit ? (
              <FormKodeRosokEdit onSubmit={(data) => this.handleEdit(data)} />
            ) : (
              <FormKodeRosok onSubmit={(data) => this.handleSubit(data)} />
            )
          }
          title={
            this.state.isEdit
              ? "Edit Kode Rongsok Dan Ciok"
              : "Tambah Kode Rongsok Dan Ciok"
          }
        />
        <TourGuide
          tourConfig={[
            `Untuk Menambahkan Data Kode Rongsok / Ciok, Tekan Tombol ini Dan Isi kolom yang disediakan`,
            `Ini adalah data Kode Rongsok / Ciok yang sudah tersimpan`,
            `Ini adalah tombol untuk menghapus data pada baris yang dipilih`,
            `Ini adalah tombol untuk mengubah data pada baris yang dipilih`,
          ]}
        />
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Data Kode Rosok Dan Ciok" />
              <div className="card-body">
                <form action="#">
                  <div className="form-body">
                    <div className="col-lg-12 col-md-12">
                      <div className="text-right">
                        <button
                          name="simpan"
                          id="simpan"
                          type="button"
                          className="btn btn-info"
                          data-tut="reactour_1"
                          onClick={() => this.tambahModal()}
                        >
                          Tambah Data Rosok / Ciok
                        </button>
                      </div>
                    </div>
                  </div>
                </form>
                <div data-tut="reactour_2">
                  {this.props.KodeRosokList ? (
                    <ToolkitProvider
                      keyField="id"
                      data={this.props.KodeRosokList}
                      columns={this.state.columns}
                      search
                    >
                      {(props) => (
                        <div className="form-group">
                          <div className="text-right mt-5 mb-3">
                            <SearchBar
                              {...props.searchProps}
                              className="form-control"
                              placeholder="Pencarian"
                              style={{ margin: 0, top: "50%" }}
                            />
                          </div>
                          <BootstrapTable
                            {...props.baseProps}
                            striped
                            hover
                            condensed
                            bordered={true}
                            headerClasses="header-table"
                            pagination={paginationFactory()}
                          />
                          <ExportCSVButton
                            {...props.csvProps}
                            className="btn btn-primary"
                          >
                            Export CSV
                          </ExportCSVButton>
                        </div>
                      )}
                    </ToolkitProvider>
                  ) : (
                    <div>
                      {this.props.error ? (
                        <div className="text-center">
                          <img
                            src={notrespond}
                            style={{ width: "30%" }}
                            alt="NOT RESPOND"
                          />
                          <br />
                          <h4>Request API Gagal, Silahkan Refresh</h4>
                        </div>
                      ) : (
                        <Skeleton width={"100%"} height={180} />
                      )}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(Koderosok);
